import React ,{useEffect ,useState} from 'react'
import { useSelector,useDispatch} from 'react-redux';


export default function Cart() {
    const {cart} =useSelector(state=>state)
    const dispatch=useDispatch()
    const [total,setTotal]=useState(0)
    useEffect(()=>{
let sum=cart.reduce((acc,item)=>{
acc=acc+item.price*item.item
return acc

},0)
setTotal(sum)
    },[cart])
  
    return (
        <div >
           <h1 style={{textAlign:"center"}}>Added to Cart</h1>
          
           <div className="cart-item"> 
        {cart.map((item)=>{
            return (<>
             <div className="cart-div">
                 <img src={item.img} alt=""/>
                 <h3>{item.name}</h3>
                 <p>{item.description}</p>
                 <h3>$ {item.price} X {item.item}</h3>
                 <p>Product :   {item.name}</p>
                

                <button onClick={()=>dispatch({type:"deleteitem",payload:item})}><i class="fas fa-trash-alt"></i></button>
                </div> 
                
                </>
            )
        })}
        </div> 
        <div style={{border:"1px solid",padding:"20px",color:"blue"}}>
        <h2>Total: $ {total}</h2>
        <button>Checkout</button>
         </div>
       
        </div>
        

    )
}